@import '../common/common.scss';

.invoice-view {
  padding: 20px 0 0 10px;
  width: 100%;
  .up-icon {
    color: $bg-page;
    text-align: center;
    margin-bottom: 10px;
    cursor: pointer;
  }
  & svg:hover {
    color: $bg-page-on;
  }
  .invoice-title {
    text-align: center;
  }
  .invoice-table {
    border-collapse: collapse;
    width: calc(100% - 20px);
    & tr {
      border-bottom: 1px solid $border-color;
    }
    & th, td {
      text-align: left;
      padding: 5px 10px;
    }
  }
}