@import '../common/common.scss';

.block-view {
  position: absolute;
  left: 0;
  right: 20px;
  padding: 10% 0 0 10px;
  .up-icon {
    color: $bg-page;
    text-align: center;
    margin-bottom: 10px;
    & svg {
      cursor: pointer;
    }
  }
  .page-view {
    display: grid;
    grid-template-columns: 50px auto 50px;
  }
  .block-panel {
    display: flex;
    flex-direction: row;
    overflow-x: auto;
  }
  .block-chain {
    display: grid;
    grid-template-columns: 150px 50px;
  }
  .block-container {
    font-size: 14px;
    width: 150px;
    margin-right: 40px;
  }
  .info-container {
    padding: 0 5px 5px 5px;
    display: grid;
    & .icon-down {
      color: $bg-page;
      text-align: center;
      justify-self: center;
      cursor: pointer;
      &:hover {
        color: $bg-page-on;
      }
    }
  }
  .block-image {
    width: 150px;
    user-select: none;
  }
  .chain-image {
    width: 50px;
    margin-top: 75px;
    user-select: none;
  }
  .block-chain-page-icon {
    cursor: pointer;
    color: $bg-page;
    margin-top: 55px;
  }
  .bc-page-icon-disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
  .paginator-panel {
    position: relative;
    .paginator {
      position: absolute;
      right: 100px;
      display: flex;
      flex-direction: row;
      align-items: center;
    }
    .page-block {
      padding: 5px 10px;
      margin: 5px;
      background-color: $bg-page;
      color: white;
      cursor: pointer;
      &:hover {
        background-color: $bg-page-on;
      }
    }
    .page-icon {
      cursor: pointer;
      color: $bg-page;
      &:hover {
        color: $bg-page-on;
      }
    }
    .page-highlighted {
      background-color: $bg-page-on;
    }
  }
  & svg:hover {
    color: $bg-page-on;
  }
}