@import '../common/common.scss';

.pager-view {
  .paginator {
    position: absolute;
    right: 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .page-block {
    padding: 5px 10px;
    margin: 5px;
    background-color: $bg-page;
    color: white;
    user-select: none;
    cursor: pointer;
    &:hover {
      background-color: $bg-page-on;
    }
  }
  .page-icon {
    cursor: pointer;
    color: $bg-page;
    user-select: none;
    &:hover {
      color: $bg-page-on;
    }
  }
  .page-highlighted {
    background-color: $bg-page-on;
  }
  & svg:hover {
    color: $bg-page-on;
  }
}